    const navigateTo = (path) => {
      router.push(path);
    };

    onMounted(() => {
      getHeroInfo();
      getServiceInfo();
      getContactInfo();
    });

    watch(locale, () => {
      getHeroInfo();
      getServiceInfo();
      getContactInfo();
    });

    return {
      heroInfo,
      serviceInfo,
