        {{ $t("contact-review-title") }}</el-col
      >
    </el-row>
    <el-divider />
    <div class="contact-content">
      <!-- title2 -->
      <el-row>
        <el-col :span="24" :xs="24" class="contact-title2">
          {{ $t("contact-review-title2") }}</el-col
        >
      </el-row>
      <el-row>
        <el-col :span="24" :xs="24" class="contact-title2">
          {{ $t("contact-review-title3") }}</el-col
        >
      </el-row>
      <el-divider />
      <!-- 氏名 -->
      <el-row>
        <el-col :span="6" :xs="6" class="contact-field">
          {{ $t("contact-name") }}</el-col
        >
        <el-col :span="18" :xs="18" class="">
          <el-input
            v-model="contactInfo.name"
            class="contact-input"
            size="large"
            disabled
          />
        </el-col>
      </el-row>
      <el-divider />
      <!-- E-mail -->
      <el-row>
        <el-col :span="6" :xs="6" class="contact-field">
          {{ $t("contact-email") }}</el-col
        >
        <el-col :span="18" :xs="18" class="">
          <el-input
            v-model="contactInfo.mail"
            class="contact-input"
            size="large"
            disabled
          />
        </el-col>
      </el-row>
      <el-divider />
      <!-- お問い合わせ内容 -->
      <el-row>
        <el-col :span="6" :xs="6" class="contact-field">
          {{ $t("contact-content") }}</el-col
        >
        <el-col :span="18" :xs="18" class="">
          <el-input
            v-model="contactInfo.content"
            class="contact-input"
            size="large"
            maxlength="500"
            show-word-limit
            type="textarea"
            :rows="8"
            disabled
          />
        </el-col>
      </el-row>
      <el-divider />
      <el-row justify="center">
        <el-button size="large" class="contact-goback-btn" @click="goback()">{{
          $t("contact-review-goback-btn")
        }}</el-button>
        <el-button size="large" class="contact-btn" @click="send()">{{
          $t("contact-review-btn")
        }}</el-button>
      </el-row>
    </div>
    <el-dialog
      v-model="centerDialogVisible"
      title="Warning"
      width="500"
      align-center
    >
      <span>Open the dialog from the center from the screen</span>
      <template #footer>
        <div class="dialog-footer">
          <el-button @click="centerDialogVisible = false">Cancel</el-button>
          <el-button type="primary" @click="centerDialogVisible = false">
            Confirm
          </el-button>
        </div>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import { ref, onMounted, watch } from "vue";
import { useRoute, useRouter } from "vue-router";
import { ElMessage } from "element-plus";
import { useI18n } from "vue-i18n";
export default {
  setup() {
    const router = useRouter();
    // 使用 useRoute 获取路由对象
    const route = useRoute();
    const { t } = useI18n();
    //
    const contactInfo = ref({});
    const goback = () => {
      router.back(); // 返回上一个页面
    };
    const send = () => {
      ElMessage({
        message: t("success-message"),
        type: "success",
      });

      localStorage.removeItem("contactInfo");
      router.back(); // 返回上一个页面
    };

    onMounted(() => {
      // 获取 query 参数
      contactInfo.value = route.query;
    });
    return {
      contactInfo,
      goback,
      send,
    };
  },
};
</script>

