      }));
      // 模拟异步操作
      await new Promise((resolve) => setTimeout(resolve, 1000)); // 模拟 2 秒加载时间
      // 关闭 loading 动画
      loading.close();
    };
    const clealAll = () => {
      conditions.value = {
        manufacturer: "00", // メーカー
        made: "00", // 車種
        year: "00", // 年份
        priceFrom: "00", // 价格from
        priceTo: "00", // 价格to
      };
    };

    const goToCarInfo = (id) => {
      const carInfo = carListTmp.value.find((car) => car.id === id);
      localStorage.setItem("carInfo", JSON.stringify(carInfo));
      router.push("/carInfo");
    };
    onMounted(async () => {
      await getAllList();
      search();
    });

    watch(locale, async () => {
      await getAllList();
      search();
    });

    return {
      manufacturerList,
      modelList,
      yearList,
      priceList,
      conditions,
      carList,
      search,
      clealAll,
      goToCarInfo,
    };
  },
};
</script>

