<script>
import { ref } from "vue";
import { useRouter } from "vue-router";
import { Menu } from "@element-plus/icons-vue"; // 引入 Element Plus 图标库中的 Menu 图标
import { useI18n } from "vue-i18n";

export default {
  components: {
    Menu,
  },
  setup() {
    const { locale } = useI18n();
    const drawer = ref(false); // 控制抽屉显示
    const currentLanguage = ref("English");
    const router = useRouter();

    // PC模式下页面跳转
    const navigateTo = (path) => {
      router.push(path);
    };
    // 手机模式 页面跳转
    const navigateTo2 = (path) => {
      drawer.value = false; // 关闭抽屉
      router.push(path);
    };

    // サービス遷移
    const handleSalesChange = (path) => {
      router.push(path);
    };

    const handleLanguageChange = (language) => {
      if (language === "en") currentLanguage.value = "English";
      else if (language === "ja") currentLanguage.value = "日本語";
      else if (language === "zh") currentLanguage.value = "中文";
      locale.value = language; // 切换语言
    };

    const handleMobileMenu = (language) => {
      if (language === "en") currentLanguage.value = "English";
      else if (language === "ja") currentLanguage.value = "日本語";
      else if (language === "zh") currentLanguage.value = "中文";
      locale.value = language; // 切换语言
      drawer.value = false; // 关闭抽屉
    };

    const isActive = (path) => {
      return router.currentRoute.value.path === path;
    };

    const isActive2 = (language) => {
      return locale.value === language;
    };

    return {
      drawer,
      currentLanguage,
      navigateTo,
      navigateTo2,
      handleSalesChange,
      handleLanguageChange,
      handleMobileMenu,
      isActive,
      isActive2,
    };
  },
};
</script>

