        >
      </el-row>
      <el-divider />
      <!-- 本社＆拠点 -->
      <el-row>
        <el-col :span="12" :xs="12" style="font-weight: bold">
          {{ $t("companyInfo-headquarters-and-offices") }}</el-col
        >
        <el-col :span="12" :xs="12" class="">
          {{ companyInfo.headquartersAndOffices }}</el-col
        >
      </el-row>
      <el-divider />
      <!-- 海外オフィス -->
      <el-row>
