<script>
import { ref, onMounted, watch } from "vue";
import { useI18n } from "vue-i18n";
export default {
  setup() {
    const { locale } = useI18n();
    // 主区域情报
    const companyInfo = ref({});
    const getCompanyInfo = () => {
      const info = {
        companyName_ja: "株式会社盛栄国際",
        companyName_en: "Seiei International Corporation",
        headquartersAndOffices_ja: "仙台、大宮、名古屋、大阪、福岡",
        headquartersAndOffices_en: "Sendai, Omiya, Nagoya, Osaka, Fukuoka",
        overseasOffices_ja: "香港、カナダ ",
        overseasOffices_en: "Hong Kong ,  Canada",
        fiscalYear_ja: "3月31日",
        fiscalYear_en: "March 31 ",
        businessContent_ja: "自動車関連全般",
        businessContent_en: "Automobile-related businesses in general",
        address_ja: "xxxxxxxxxxx",
        address_en: "xxxxxxxxxxx",
      };
      if (locale.value === "ja") {
        companyInfo.value.companyName = info.companyName_ja;
        companyInfo.value.headquartersAndOffices =
          info.headquartersAndOffices_ja;
        companyInfo.value.overseasOffices = info.overseasOffices_ja;
        companyInfo.value.fiscalYear = info.fiscalYear_ja;
        companyInfo.value.businessContent_ = info.businessContent_ja;
        companyInfo.value.address = info.address_ja;
      } else {
        companyInfo.value.companyName = info.companyName_en;
        companyInfo.value.headquartersAndOffices =
          info.headquartersAndOffices_en;
        companyInfo.value.overseasOffices = info.overseasOffices_en;
        companyInfo.value.fiscalYear = info.fiscalYear_en;
        companyInfo.value.businessContent_ = info.businessContent_en;
        companyInfo.value.address = info.address_en;
      }
    };
    onMounted(() => {
      getCompanyInfo();
    });
    watch(locale, () => {
      getCompanyInfo();
    });
    return {
      companyInfo,
    };
  },
};
</script>

